// Animationen
const fadeIn = keyframes`
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
`;

const glow = keyframes`
  0%, 100% { box-shadow: 0 0 10px #00ffff; }
  50% { box-shadow: 0 0 20px #00ffff, 0 0 30px #00ffff; }
`;

// User Interface
interface User {
  id: string;
  username: string;
  email: string;
  createdAt: Date;
  stats: {
    totalGamesPlayed: number;
    totalScore: number;
    favoriteGame: string;
    highScores: Record<string, number>;
    achievements: string[];
  };
}

// User Context
interface UserContextType {
  currentUser: User | null;
  login: (user: User) => void;
  logout: () => void;
  updateStats: (gameType: string, score: number) => void;
  updateHighScore: (gameType: string, score: number) => boolean;
  isLoggedIn: boolean;
}

const UserContext = createContext<UserContextType | null>(null);

// Custom Hook fr User Context
export const useUser = () => {
  const context = useContext(UserContext);
  if (!context) {
    throw new Error('useUser must be used within a UserProvider');
  }
  return context;
};

